import React from 'react';
import {
  Box,
  Container,
  Grid,
  Typography,
  Link,
  TextField,
  Button,
  IconButton,
  Divider,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import {
  Facebook,
  Twitter,
  Instagram,
  LinkedIn,
  YouTube,
  Send,
  Phone,
  Email,
  LocationOn,
  Copyright,
} from '@mui/icons-material';

function Footer() {
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: 'primary.dark',
        color: 'white',
        py: 6,
        mt: 'auto',
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          {/* Company Info Section */}
          <Grid item xs={12} md={4}>
            <Typography variant="h5" fontWeight="bold" gutterBottom>
              Your Company
            </Typography>
            <Typography variant="body2" color="grey.300" paragraph>
              We are dedicated to providing the best service to our customers. 
              Our mission is to deliver excellence in every aspect of our business.
            </Typography>
            
            <Box display="flex" alignItems="center" mb={1}>
              <LocationOn sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                123 Street Name, City, Country
              </Typography>
            </Box>
            
            <Box display="flex" alignItems="center" mb={1}>
              <Phone sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                +1 234 567 8900
              </Typography>
            </Box>
            
            <Box display="flex" alignItems="center">
              <Email sx={{ mr: 1, color: 'secondary.main' }} />
              <Typography variant="body2" color="grey.300">
                info@yourcompany.com
              </Typography>
            </Box>
          </Grid>

          {/* Quick Links Section */}
          <Grid item xs={12} sm={6} md={2}>
            <Typography variant="h6" fontWeight="bold" gutterBottom>
              Quick Links
            </Typography>
            <List dense>
              {[
                { text: 'Home', href: '/' },
                { text: 'About Us', href: '/about' },
                { text: 'Contact', href: '/contact' }
              ].map((item) => (
                <ListItem key={item.text} disableGutters>
                  <Link 
                    href={item.href} 
                    color="grey.300" 
                    underline="hover"
                    sx={{ 
                      '&:hover': { 
                        color: 'secondary.main',
                        transform: 'translateX(5px)',
                        transition: 'all 0.3s ease'
                      }
                    }}
                  >
                    {item.text}
                  </Link>
                </ListItem>
              ))}
            </List>
          </Grid>

          {/* Services Section */}
          <Grid item xs={12} sm={6} md={2}>
            <Typography variant="h6" fontWeight="bold" gutterBottom>
              Services
            </Typography>
            <List dense>
              {['Web Design', 'Development', 'Marketing', 'Consulting', 'Support'].map((text) => (
                <ListItem key={text} disableGutters>
                  <Link 
                    href="#" 
                    color="grey.300" 
                    underline="hover"
                    sx={{ 
                      '&:hover': { 
                        color: 'secondary.main',
                        transform: 'translateX(5px)',
                        transition: 'all 0.3s ease'
                      }
                    }}
                  >
                    {text}
                  </Link>
                </ListItem>
              ))}
            </List>
          </Grid>

          {/* Newsletter Section */}
          <Grid item xs={12} md={4}>
            <Typography variant="h6" fontWeight="bold" gutterBottom>
              Newsletter
            </Typography>
            <Typography variant="body2" color="grey.300" paragraph>
              Subscribe to our newsletter for updates and offers.
            </Typography>
            
            <Box component="form" noValidate>
              <Box display="flex" gap={1}>
                <TextField
                  fullWidth
                  size="small"
                  placeholder="Your email"
                  variant="outlined"
                  sx={{
                    backgroundColor: 'white',
                    borderRadius: 1,
                    '& .MuiOutlinedInput-root': {
                      '& fieldset': {
                        borderColor: 'transparent',
                      },
                      '&:hover fieldset': {
                        borderColor: 'secondary.main',
                      },
                    },
                  }}
                />
                <Button
                  variant="contained"
                  color="secondary"
                  sx={{
                    minWidth: 'auto',
                    px: 2,
                  }}
                >
                  <Send />
                </Button>
              </Box>
            </Box>

            {/* Social Media Icons */}
            <Box mt={3}>
              <Typography variant="h6" fontWeight="bold" gutterBottom>
                Follow Us
              </Typography>
              <Box display="flex" gap={2}>
                {[
                  { icon: <Facebook />, label: 'Facebook', color: '#1877F2' },
                  { icon: <Twitter />, label: 'Twitter', color: '#1DA1F2' },
                  { icon: <Instagram />, label: 'Instagram', color: '#E4405F' },
                  { icon: <LinkedIn />, label: 'LinkedIn', color: '#0A66C2' },
                  { icon: <YouTube />, label: 'YouTube', color: '#FF0000' },
                ].map((social) => (
                  <IconButton
                    key={social.label}
                    aria-label={social.label}
                    sx={{
                      backgroundColor: 'rgba(255, 255, 255, 0.1)',
                      color: 'white',
                      '&:hover': {
                        backgroundColor: social.color,
                        transform: 'translateY(-3px)',
                        transition: 'all 0.3s ease',
                      },
                    }}
                  >
                    {social.icon}
                  </IconButton>
                ))}
              </Box>
            </Box>
          </Grid>
        </Grid>

        <Divider sx={{ my: 4, backgroundColor: 'rgba(255, 255, 255, 0.1)' }} />

        {/* Copyright Section */}
        <Box
          display="flex"
          flexDirection={{ xs: 'column', sm: 'row' }}
          justifyContent="space-between"
          alignItems="center"
          color="grey.400"
        >
          <Box display="flex" alignItems="center" mb={{ xs: 1, sm: 0 }}>
            <Copyright sx={{ fontSize: 14, mr: 0.5 }} />
            <Typography variant="body2">
              2024 Your Company. All rights reserved.
            </Typography>
          </Box>
          
          <Box display="flex" gap={2}>
            <Link href="#" color="inherit" underline="hover" variant="body2">
              Privacy Policy
            </Link>
            <Link href="#" color="inherit" underline="hover" variant="body2">
              Terms of Service
            </Link>
            <Link href="#" color="inherit" underline="hover" variant="body2">
              Cookie Policy
            </Link>
          </Box>
        </Box>
      </Container>
    </Box>
  );
}

export default Footer;